.matrix-table2{
    text-align: center;
    margin: auto;
    width:360px;
    background-color: #1d3557;
  }
  .size-tr{
    width: 8px;
  }
  
  .dark{
    background-color: #457b9d;
  }
  
  .light{
    background-color: #a8dadc;
  }
  
  .center{
    text-align: center;
    margin: 0;
  }
  
  input{
    background-color: transparent;
    width: 100%;
    border: transparent;
    text-align: center;
  }
  
  .centrar-btn{
    background-color: #e63946;
    border-color: #1d3557;
    padding: 7px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    color: black;
  }
  
  .App{
    text-align: center;
  }
  
  .top-3{
    margin-top: 3rem;
  }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
} 

input:focus{
  outline: none;
}
th:hover{
  background-color: #f1faee;
  opacity: 0.6;
}